<template>
  <div class="content" v-swipeup="{fn:vuetouch,name:'上滑'}">
    <div class="head">
      <img src="../../../static/image/icon_back.png" alt class="back" @click="back" />
      积分记录
      <img src="../../../static/image/icon_add.png" alt class="add" @click="add" />
    </div>

    <div class="main">
      <div class="list" v-for="i in list">
        <div class="list1">
          <div>
            <p>来源</p>
            <p>{{i.content}}</p>
            <!-- <p v-if="i.extends == 'merchant_mining_shou'">增加积分</p>
            <p v-if="i.extends == 'merchant_mining_reduce'">减少积分</p>-->
            <!-- <p>线上充值</p> -->
          </div>
          <div>
            <p>变动金额</p>
            <p>{{ i.money | numFilter}}</p>
          </div>
          <div>
            <p>变动后</p>
            <p style="color: #46ffde;">{{jiequs(i.before*1,i.money*1)}}</p>
          </div>
        </div>
        <div class="list2">计算日期：{{$times(i.create_time)}}</div>
      </div>
      <div class="mask" v-if="mask" @touchmove.prevent @click="closewin"></div>
      <div class="win" v-if="mask">
        <div class="win_box">
          <p>转入数量</p>
          <input type="text" v-model="num" />
        </div>
        <div class="win_box">
          <p>手机号</p>
          <input type="text" v-model="phone" />
        </div>
        <div class="win_box">
          <p>交易密码</p>
          <input type="password" v-model="password" />
        </div>
        <div class="win_send" @click="zhuanru">确认转入</div>
      </div>

      <div class="mask" v-if="pass_boon"></div>
      <div class="pass_win" v-if="pass_boon">
        <div class="pass_title">输入交易密码</div>
        <input type="password" v-model="paypassword" placeholder="请输入交易密码" />
        <div class="btns">
          <p @click="closepass">取消</p>
          <p @click="sendpass">确定</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
      list: [],
      page: 1,
      mask: false,
      num: "",
      password: "",
      touch_index: null, //滑动列表对应下标
      touch_index1: null, //出现删除按钮使用的控制变量
      paypassword: "",
      pass_boon: false,
      mask_jack: {},
      status: null, //数据状态
      phone: '',
    };
  },
  components: {},
  filters: {
			numFilter(value) {
				let realVal = ''
				if (value) {
					// 截取当前数据到小数点后两位
					realVal = parseFloat(value).toFixed(2)
				} else {
					realVal = '--'
				}
				return realVal
			}
		},
  methods: {
    jiequs(str1, str2) {
      let str = str1 + str2;
      if (String(str).indexOf(".") > -1) {
        var temp = Number(str);
        temp = Math.floor(temp * 1000) / 1000;
        temp = temp.toFixed(4);
        return temp;
      } else {
        return str;
      }
    },
    back() {
      this.$router.go(-1);
    },
    getlist() {
      let that = this;
      that
        .$post("/apiv1/order/getTransRecordad", {
          page: that.page
        })
        .then(res => {
          console.log(res);
          if (res.code == 1) {
            if (res.data.length) {
              that.list = that.list.concat(res.data);
            } else {
              if (that.page > 1) {
                that.$msg("暂无更多数据");
              }
            }
          }
        });
    },
    add() {
      this.mask = true;
    },
    closewin() {
      //关闭蒙层弹窗
      this.mask = false;
    },
    zhuanru() {
      //转入
      let that = this;
      if (that.num == "") {
        that.$msg("请输入交易数量");
      } else if (that.password == "") {
        that.$msg("请输入交易密码");
      }else if (that.phone == "") {
        that.$msg("请输入手机号");
      } else {
        that
          .$post("/apiv1/index/zhuanzhang", {
            type: 1,
            money: that.num,
            paypassword: that.password,
            username: that.phone
          })
          .then(res => {
            that.$msg(res.msg);
            if (res.code == 1) {
              console.log(res);
              that.mask = false;
              that.list = [];
              that.page = 1;
              that.getlist();
            }
          });
      }
    },
    vuetouch: function(s, e) {
      this.page++;
      this.getlist();
    }
  },
  created() {
    this.getlist();
  },
  destroyed() {}
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin";
.mask {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background: #000;
  opacity: 0.8;
}
.win {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  background: #1e2a5e;
  border-top-left-radius: 40px / $ppr;
  border-top-right-radius: 40px / $ppr;
  z-index: 1000;
  padding: 20px / $ppr 0;
  .win_box {
    width: 660px / $ppr;
    height: 80px / $ppr;
    margin: 0 auto;
    border-bottom: 1px solid #fff;
    color: #fff;
    clear: both;
    p {
      width: 120px / $ppr;
      height: 80px / $ppr;
      line-height: 80px / $ppr;
      float: left;
      font-size: 24px / $ppr;
    }
    input {
      display: block;
      float: left;
      width: 500px / $ppr;
      height: 80px / $ppr;
      border: none;
      outline: none;
      background: #1e2a5e;
      color: #fff;
    }
  }
  .win_send {
    width: 660px / $ppr;
    height: 80px / $ppr;
    background: #0293fc;
    text-align: center;
    line-height: 80px / $ppr;
    text-align: center;
    font-size: 28px / $ppr;
    color: #fff;
    clear: both;
    margin: 0 auto;
    border-radius: 20px / $ppr;
    margin-top: 20px / $ppr;
  }
}
.content {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background: #0d1637;
  .head {
    width: 100%;
    height: 110px / $ppr;
    position: relative;
    text-align: center;
    line-height: 110px / $ppr;
    font-size: 36px / $ppr;
    color: #fff;
    background: #0d1637;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    .back {
      width: 17px / $ppr;
      height: 29px / $ppr;
      display: block;
      position: absolute;
      top: 40px / $ppr;
      left: 44px / $ppr;
    }
    .add {
      width: 44px / $ppr;
      height: 44px / $ppr;
      display: block;
      position: absolute;
      top: 36px / $ppr;
      right: 40px / $ppr;
    }
  }
  .main {
    width: 675px / $ppr;
    margin: 0 auto;
    padding: 120px / $ppr 0;
    .list {
      width: 100%;
      height: 155px / $ppr;
      margin-bottom: 15px / $ppr;
      background: #313f79;
      border-radius: 10px / $ppr;
      .list1 {
        width: 100%;
        height: 108px / $ppr;
        border-bottom: 2px / $ppr solid #4758a1;
        div {
          width: 33.3%;
          height: 108px / $ppr;
          float: left;
          p:nth-child(1) {
            width: 100%;
            height: 55px / $ppr;
            line-height: 60px / $ppr;
            text-align: center;
            font-size: 24px / $ppr;
            color: #92aaff;
          }
          p:nth-child(2) {
            width: 100%;
            text-align: center;
            font-size: 20px / $ppr;
            color: #fff;
          }
        }
      }
      .list2 {
        padding-left: 22px / $ppr;
        line-height: 44px / $ppr;
        font-size: 18px / $ppr;
        color: #b2c1ff;
      }
    }
  }
}
</style>



